<template>
  <div class="uc-popup-calendar">
    <input-group
      v-bind="{ label, hasErrors, readonly }"
      tag="div">
      <date-picker
        v-model="inputValue"
        class="date-picker"
        :disabled="readonly"
        :format="formatter"
        full-month-name
        :placeholder="dateFormat"
        monday-first
        calendar-class="calendar"
        wrapper-class="wrapper"
        input-class="datepicker-input"
        @selected="onSelected" />
    </input-group>
  </div>
</template>

<script>
import DatePicker from 'vuejs-datepicker'
import { InputGroup } from '../input-group'
import { Format as format, locales } from '@/utils/date-formatter'

const dateFormats = {
  default: 'MMMM D YYYY',
}

export default {
  name: 'UcPopupCalendar',
  components: {
    DatePicker,
    InputGroup,
  },
  props: {
    label: {
      type: String,
      default: '',
    },
    value: {
      type: [Date, String],
      default: null,
    },
    hasErrors: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    language: {
      type: String,
      default: '',
    },
  },
  data () {
    return {
      inputValue: '',
    }
  },
  computed: {
    dateFormat () {
      return dateFormats[this.language] || dateFormats.default
    },
  },
  watch: {
    value: {
      immediate: true,
      handler (newValue) {
        this.inputValue = newValue
      },
    },
  },
  methods: {
    formatter (date) {
      return format(date, this.dateFormat, locales[this.language])
    },
    onSelected (date) {
      this.$emit('selected', date)
    },
  },
}
</script>
<!-- eslint-disable vue-scoped-css/require-scoped -->
<style lang="stylus">
.date-picker
  width 100%

  .vdp-datepicker__calendar .cell:not(.blank):not(.disabled).day:hover
  .vdp-datepicker__calendar .cell:not(.blank):not(.disabled).month:hover
  .vdp-datepicker__calendar .cell:not(.blank):not(.disabled).year:hover
    border 1px solid $base-color-brand-50

  .vdp-datepicker__calendar header .prev:not(.disabled):hover
  .vdp-datepicker__calendar header .next:not(.disabled):hover
  .vdp-datepicker__calendar header .up:not(.disabled):hover
    background $base-color-brand-50

  .cell.day.selected
  .cell.month.selected
  .cell.year.selected
  .vdp-datepicker__calendar .cell.selected:hover
    background $base-color-brand-50

  .calendar
    width 100%
    max-width 400px
    border-color $base-color-gray-400
    border-radius 4px

  .datepicker-input
    width 100%
    height 48px
    padding 13px 16px 15px
    border none
    border-radius 4px
    background-color transparent
    outline none
    cursor pointer
    primary-body-text()

    &::placeholder
      secondary-body-text()

    &:disabled
      color #cacad7
</style>
