<template>
  <div class="uc-single-select-editor">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="formattedValue"
      :required="isRequired">
      <uc-select
        v-bind="{ options, placeholder }"
        :selected="selectedGuid"
        :readonly="isReadonly"
        :nullable="!isRequired"
        @selected="onSelected" />
    </editor-wrapper>
  </div>
</template>

<script>
import UcSelect from '@/components/forms/uc-select'
import { isEmpty } from '@/utils/object.utils.js'
import editorMixin from '../editor-mixin'

export default {
  name: 'UcSingleSelectEditor',
  components: {
    UcSelect,
  },
  mixins: [editorMixin],
  data () {
    return {
      selectedOption: {},
    }
  },
  computed: {
    formattedValue () {
      if (isEmpty(this.selectedOption)) {
        return ''
      }

      return this.selectedOption.label
    },
    placeholder () {
      return this.definition.label || this.$t('global.selectPlaceholder')
    },
    selectedGuid () {
      return this.definition.selected === undefined ? '' : this.definition.selected[0]
    },
  },
  watch: {
    definition: {
      handler () {
        this.selectedOption = this.options.find(option => option.guid === this.selectedGuid)
      },
      immediate: true,
      deep: true,
    },
  },
  methods: {
    onSelected (event) {
      const oldSelectedOption = this.selectedOption
      const emptyOption = !this.isRequired ? [] : undefined
      this.selectedOption = event

      this.onChange({
        label: this.placeholder,
        newValue: !isEmpty(this.selectedOption) ? [this.selectedOption.guid] : [],
        oldValue: !isEmpty(oldSelectedOption) ? [oldSelectedOption.guid] : emptyOption,
        valueFieldName: 'selected',
      })
    },
  },
}
</script>
