<template>
  <article class="navigation-layout">
    <sticky-header>
      <button-toggle-group
        v-model="navigationType"
        :values="navigation"
        @input="onToggle">
        <template #default="{ value }">
          <span
            :data-test="value"
            class="navigation-layout__icon">
            <component :is="getIconType(value)" />
          </span>
          {{ value }}
        </template>
      </button-toggle-group>
      <template #actions>
        <div class="navigation-layout__selector">
          <store-selector />
        </div>
        <language-selector />
      </template>
    </sticky-header>
    <base-layout>
      <slot />
    </base-layout>
  </article>
</template>

<script>
import BaseLayout from './_base-layout.vue'
import StickyHeader from '@/components/widgets/sticky-header'
import ButtonToggleGroup from '@/components/generic/button-toggle-group'
import LanguageSelector from '@/components/widgets/language-selector.vue'
import StoreSelector from '@/components/widgets/store-selector.vue'
import { Search24Normal as SearchIcon, Tree24Normal as TreeIcon } from '@/assets/icons'

const ComponentEvent = {
  TOGGLE: 'toggle',
}

const NavigationType = {
  SEARCH: 'Search',
  NAVIGATE: 'Navigate',
}

const IconType = {
  SEARCH: 'search-icon',
  NAVIGATE: 'tree-icon',
}

export default {
  name: 'NavigationLayout',
  components: {
    StickyHeader,
    BaseLayout,
    ButtonToggleGroup,
    SearchIcon,
    TreeIcon,
    LanguageSelector,
    StoreSelector,
  },
  data () {
    return {
      navigationType: NavigationType.SEARCH,
    }
  },
  computed: {
    navigation () {
      return Object.values(NavigationType)
    },
  },
  methods: {
    getIconType (type) {
      const navigationKey = Object.keys(NavigationType).find(key => NavigationType[key] === type)
      return IconType[navigationKey]
    },
    onToggle (value) {
      this.$emit(ComponentEvent.TOGGLE, value)
    },
  },
}
</script>

<style lang="stylus" scoped>
.navigation-layout
  padding-bottom 4 * $base-unit

.navigation-layout__icon
  display inline-flex
  margin-right ($base-unit / 2)

.navigation-layout__selector
  margin-right 2 * $base-unit
</style>
