<template>
  <div class="uc-multi-select-editor">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="formattedValue"
      :required="isRequired">
      <uc-dropdown
        :options="options"
        :readonly="isReadonly"
        :placeholder="label"
        :name="placeholder"
        :selected="hasSelectedOptions">
        <template #default="{ option, searchTerm }">
          <uc-checkbox
            :guid="option.guid"
            :value="option.guid"
            :label="option.label"
            :checked="setChecked(option.guid)"
            :readonly="setReadonly(option.guid)"
            @change="onOptionChange">
            <!-- eslint-disable-next-line vue/no-v-html -->
            <span v-html="highlight(option.label, searchTerm)" />
          </uc-checkbox>
        </template>
      </uc-dropdown>
    </editor-wrapper>
  </div>
</template>

<script>
import UcCheckbox from '@/components/forms/uc-checkbox'
import UcDropdown from '@/components//forms/uc-dropdown'
import highlight from '@/utils/word-highlight'
import editorMixin from '../editor-mixin'

export default {
  name: 'UcMultiSelectEditor',
  components: {
    UcCheckbox,
    UcDropdown,
  },
  mixins: [editorMixin],
  data () {
    return {
      selectedOptions: [], // Array of optionObjects { guid: String, label: String }
    }
  },
  computed: {
    formattedValue () {
      if (!this.hasSelectedOptions) {
        return ''
      }
      return this.label
    },
    placeholder () {
      return this.definition.label || this.$t('global.selectPlaceholder')
    },
    label () {
      if (this.selectedOptions.length === 0) {
        return this.placeholder
      }

      if (this.selectedOptions.length === 1) {
        return this.selectedOptions[0].label
      }

      return `${this.selectedOptions[0].label} and ${this.selectedOptions.length - 1} more`
    },
    hasSelectedOptions () {
      return this.selectedOptions.length > 0
    },
  },
  watch: {
    definition: {
      handler () {
        this.selectedOptions = this.definition.selected ? this.options.filter(option => option.label && this.definition.selected.includes(option.guid)) : []
      },
      immediate: true,
      deep: true,
    },
  },
  methods: {
    highlight,
    setChecked (optionGuid) {
      return this.selectedOptions ? this.selectedOptions.map(selectedOption => selectedOption.guid).includes(optionGuid) : false
    },
    setReadonly (optionGuid) {
      return this.isRequired && this.selectedOptions.length === 1 && this.setChecked(optionGuid)
    },
    setSelectedOptions (event) {
      if (event.value) {
        return [...this.selectedOptions, event]
      }

      return this.selectedOptions.filter((option) => option.guid !== event.guid)
    },
    onOptionChange (event) {
      const oldSelectedOptions = this.selectedOptions
      this.selectedOptions = this.setSelectedOptions(event)
      const emptyOption = !this.isRequired ? [] : undefined

      this.onChange({
        label: this.placeholder,
        newValue: this.selectedOptions.map(item => item.guid),
        oldValue: oldSelectedOptions.length > 0 ? oldSelectedOptions.map(item => item.guid) : emptyOption,
        valueFieldName: 'selected',
      })
    },
  },
}
</script>

<style lang="stylus" scoped>
>>> .highlight
  font-weight bold
</style>
