<template>
  <div class="settings">
    <v-popover
      :offset="12"
      popover-class="settings-popover"
      popover-inner-class="tooltip-inner popover-inner custom-scroll-bar"
      @update:open="toggleFocus($event)">
      <!-- Trigger button -->
      <uc-button-round
        theme="ghost"
        size="m">
        <i class="settings__icon">
          <truncate-icon />
        </i>
      </uc-button-round>
      <template #popover>
        <!-- Search input -->
        <div class="settings__search">
          <use-fuzzy-search
            :keys="['label']"
            :list="items"
            @search="onSearch">
            <template #default="searchProps">
              <uc-search
                dark
                dense
                :value="searchProps.value"
                :focus="isFocused"
                :clear="!isFocused"
                @input="searchProps.setValue($event, value)" />
            </template>
          </use-fuzzy-search>
        </div>

        <!-- Empty search -->
        <p
          v-show="!hasSearchResults"
          class="settings__no-results">
          {{ $t('global.searchNoResults') }}
        </p>

        <!-- Search results -->
        <uc-list-selectable
          v-show="hasSearchResults"
          :value="value"
          item-key="guid"
          :items="searchResults"
          :sync="false"
          @input="onInput"
          @select="onSelect"
          @deselect="onDeselect">
          <template #default="{ item }">
            {{ item.label }}
          </template>
        </uc-list-selectable>
      </template>
    </v-popover>
  </div>
</template>

<script>
import Vue from 'vue'
import UcButtonRound from '@/components/forms/uc-button-round'
import UcSearch from '@/components/forms/uc-search'
import UseFuzzySearch from '@/components/wrappers/use-fuzzy-search'
import UcListSelectable from '@/components/generic/uc-list-selectable'
import { Truncate16Thick as TruncateIcon } from '@/assets/icons'
import { delay } from '@/utils/async.utils'

import { VPopover, VClosePopover } from 'v-tooltip'
Vue.directive('close-popover', VClosePopover)

const ComponentEvent = {
  INPUT: 'input',
  SELECT: 'select',
  DESELECT: 'deselect',
}

export default {
  name: 'Settings',
  components: {
    UcButtonRound,
    TruncateIcon,
    UcListSelectable,
    VPopover,
    UseFuzzySearch,
    UcSearch,
  },
  props: {
    items: {
      type: Array,
      required: true,
    },
    value: {
      type: Array,
      default: () => [],
    },
  },
  data () {
    return {
      searchResults: this.items,
      isFocused: false,
    }
  },
  computed: {
    hasSearchResults () {
      return this.searchResults.length > 0
    },
  },
  methods: {
    onSearch (results) {
      this.searchResults = results
    },
    onInput (value) {
      this.$emit(ComponentEvent.INPUT, value)
    },
    onDeselect (deselectedItems) {
      this.$emit(ComponentEvent.DESELECT, deselectedItems[0])
    },
    onSelect (selectedItems) {
      this.$emit(ComponentEvent.SELECT, selectedItems[0])
    },
    async toggleFocus (isOpen) {
      // Necessary to delay, otherwise focus and clear happens to soon
      await delay(100)
      this.isFocused = isOpen
    },
  },
}
</script>

<!-- eslint-disable-next-line vue-scoped-css/require-scoped -->
<style lang="stylus">
$popover-width = $base-unit * 32

.settings-popover
  width $popover-width

  &.tooltip.popover .popover-inner
    padding $base-unit 0
</style>

<style lang="stylus" scoped>
>>> .uc-list-item
  border-bottom none

.settings__search
  margin 2 * $base-unit

.settings__no-results
  margin $base-unit 3 * $base-unit

.settings__icon svg
  position absolute
  top 50%
  left 50%
  transform translate(-50%, -70%)
</style>
