import http from '@/services/http.js'
import { EntityType } from './_entity.service.js'

import { StoreService } from './stores.service.js'
import { CatalogService } from './catalog.service.js'
import { CategoryService } from './category.service.js'
import { ProductService } from './product.service.js'
import { ProductVariantService } from './product-variant.service.js'
import { PromotionService } from './promotion.service.js'
import { CriterionService } from './criterion.service.js'
import { DiscountService } from './discount.service.js'
import { CampaignService } from './campaign.service.js'

export { EntityType } from './_entity.service.js'
export const storeService = new StoreService(http)
export const catalogService = new CatalogService(http)
export const categoryService = new CategoryService(http)
export const productService = new ProductService(http)
export const productVariantService = new ProductVariantService(http)
export const promotionService = new PromotionService(http)
export const criterionService = new CriterionService(http)
export const discountService = new DiscountService(http)
export const campaignService = new CampaignService(http)

const entityServices = {
  [EntityType.STORE]: storeService,
  [EntityType.CATALOG]: catalogService,
  [EntityType.CATEGORY]: categoryService,
  [EntityType.PRODUCT]: productService,
  [EntityType.PRODUCT_VARIANT]: productVariantService,
  [EntityType.PROMOTION]: promotionService,
  [EntityType.CRITERION]: criterionService,
  [EntityType.DISCOUNT]: discountService,
  [EntityType.CAMPAIGN]: campaignService,
}

export function getEntityService (entityType) {
  if (!(Object.values(EntityType).includes(entityType))) {
    // eslint-disable-next-line
    console.error(`[services] Could not find a registered entity service that matches the given EntityType: ${entityType}.`)
  }

  return entityServices[entityType]
}
