import deepCopy from '@/utils/deep-copy'

export function makeDefinitionsByDefinitionGuid (definitions) {
  return deepCopy(definitions).reduce((acc, definition) => {
    return { ...acc, [definition.definition.guid]: definition }
  }, {})
}

export function mergeAllData (definitionsByDefinitionGuid, entityList) {
  const propertyDefaults = {
    value: undefined,
    selected: [],
    url: undefined,
    name: undefined,
    errors: [],
  }

  return entityList.map(entity => {
    const definition = definitionsByDefinitionGuid[entity.meta.definition.guid]
    const isVariant = entity.meta.nodeType === 'productVariant'
    let mergedEntity = Object.getOwnPropertyNames(definition)

    // Filter out definitionFields based on whether it should return variant properties or product properties.
    mergedEntity = mergedEntity.filter(definitionField => definition[definitionField].isVariantProperty === null || definition[definitionField].isVariantProperty === isVariant)

    const mergedEntities = mergedEntity.reduce((acc, definitionFieldName) => {
      const definitionField = definition[definitionFieldName]
      const property = entity[definitionFieldName]
      const mergedDefinitionFieldAndProperty = { [definitionFieldName]: { ...propertyDefaults, ...definitionField, ...property } }

      return { ...acc, ...mergedDefinitionFieldAndProperty }
    }, {})

    return { ...mergedEntities, ...{ meta: entity.meta } }
  })
}
